.header {
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #4d43e5;
    color: white;
    padding: 0 10%;
    position: fixed;
    top: 0px;
    left: 0px;
  }
  .headerHeight{
    height: 100px;
  }
  .header ul {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
    align-items: center;
  }
  
  .header li {
    margin: 0 1rem;
  }
  
  .header a {
    text-decoration: none;
    color: white;
    font-size: 1.25rem;
  }
  
  .header a:hover,
  .header a:active {
    color: #b864da;
  }
  
  .header button {
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    color: #2c2922;
    margin-top: 20px;
  }
 
  .header button:hover,
  .header button:active {
    background-color: #ffa600;
    border-color: #ffa600;
  }
  